}

html,
body,
#app {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif; }

.root {
  padding: 1em; }
  .root h2 {
    margin: 0.2em 0;
    padding: 0; }
  .root .flex {
    display: -webkit-flex;
    display: flex; }

.dropdown {
  margin-bottom: 1em;
  margin-right: 1em; }
  .dropdown .dropdown-content {
    max-height: 400px;
    overflow-y: auto; }
  .dropdown .tag-item {
    min-height: 20px;
    margin: 2px; }
    .dropdown .tag-item > .search {
      padding: 4px 0 2px 2px;
      line-height: 20px;
      display: inline-block; }
  .dropdown .node .fa {
    font-size: 12px;
    margin-left: 4px;
    cursor: pointer; }
    .dropdown .node .fa-globe {
      color: darkorange; }
    .dropdown .node .fa-file-o {
      color: blue; }
    .dropdown .node .fa-copy {
      color: green; }

